export default {
	data() {
		return {
			swiperArr: [],
			dotStyle: false,
			cardCur: 0,
			list: [],
			list2: []
		};
	},
	onLoad() {
		this.selectAd();
		setTimeout(() => {
			this.selectSelfProducts();
		}, 500);
		setTimeout(() => {
			this.selectCurrPopularProducts();
		}, 1000);
	},
	methods: {
		preview(image) {
			uni.previewImage({
				urls: [image]
			});
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current;
		},
		//广告位
		async selectAd() {
			const res = await this.$util.request({
				requestUrl: 'api/ads'
			});
			console.log('广告：', res);
			if (res !== undefined) {
				this.swiperArr = res.data.data;
			}
		},
		//本店
		async selectSelfProducts() {
			const res = await this.$util.request({
				requestUrl: 'api/products',
				data: {
					limit: 6,
					page: 1,
					name: null,
					pType: 1,
					online: 1
				}
			});
			console.log('本店：', res);
			if (res !== undefined) {
				this.list2 = res.data.data;
			}
		},
		//流行
		async selectCurrPopularProducts() {
			const res = await this.$util.request({
				requestUrl: 'api/products',
				data: {
					limit: 6,
					page: 1,
					name: null,
					pType: 0,
					online: 1
				}
			});
			console.log('流行：', res);
			if (res !== undefined) {
				this.list = res.data.data;
			}
		}
	}
};
